/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  color: #1a1b15;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Yanone Kaffeesatz", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

label {
  font-size: 2rem;
}

.descripcion-pagina {
  margin: 2rem 0;
  font-family: "Poppins", sans-serif;
}

h1, h2, h3 {
  font-family: "Yanone Kaffeesatz", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #1a1b15;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 2rem;
}

a {
  color: #1a1b15;
  font-size: 2rem;
}

input {
  font-size: 2rem;
  color: #1a1b15;
  font-weight: bold;
  text-align: center;
}

.contenedor-login {
  margin: auto;
  width: 80%;
}
@media (min-width: 768px) {
  .contenedor-login {
    width: 40%;
  }
}

.form-login label {
  display: block;
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
  color: #bbbbbb;
}
.form-login .campo {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.form-login .campo input[type=text],
.form-login .campo input[type=email],
.form-login .campo input[type=password] {
  border: none;
  border-bottom: solid 1px #cb0000;
}
.form-login .btn-login {
  margin-top: 3rem;
  width: 100%;
}

.descripcion-pagina {
  background: linear-gradient(to right, #cb0000 40%, #fe0000 50%);
  color: transparent;
  background-clip: text;
  font-size: 7rem;
  text-align: center;
  margin-top: 2rem;
  font-size: 5rem;
}
@media (min-width: 768px) {
  .descripcion-pagina {
    font-size: 10rem;
  }
}
@media (min-width: 768px) {
  .descripcion-pagina {
    font-size: 4rem;
  }
}

.acciones {
  display: flex;
  justify-content: space-between;
  margin-top: 3rem;
}
.acciones .enlaces {
  font-weight: bold;
}

.btn-login {
  color: #090909;
  padding: 0.7em 1.7em;
  font-size: 18px;
  font-weight: bold;
  border-radius: 0.5em;
  background: #e8e8e8;
  border: 1px solid #e8e8e8;
  transition: all 0.3s;
  box-shadow: 6px 6px 12px #c5c5c5, -6px -6px 12px #ffffff;
}
.btn-login:hover {
  border: 1px solid white;
}
.btn-login:active {
  color: #666;
  box-shadow: inset 4px 4px 12px #c5c5c5, inset -4px -4px 12px #ffffff;
}

.btn-submit {
  color: #090909;
  padding: 0.7em 1.7em;
  font-size: 18px;
  font-weight: bold;
  border-radius: 1em;
  background: #e8e8e8ca;
  border: 1px solid #e8e8e8;
  transition: all 0.3s;
}
.btn-submit:hover {
  border: 1px solid white;
}
.btn-submit:active {
  color: #666;
  box-shadow: inset 4px 4px 12px #c5c5c5, inset -4px -4px 12px #ffffff;
}

.alerta {
  text-align: center;
  margin: 1rem auto;
  color: #ffffff;
  font-weight: bold;
  padding: 1rem;
  border-radius: 0.5rem;
}
.alerta.error {
  border-left: 20px solid #cb0000;
  background-color: #ffcbcb;
}
.alerta.exito {
  background-color: rgba(176, 255, 140, 0.7254901961);
  border-left: 20px solid #329f00;
}

.alertas div:nth-last-child(1) {
  margin-bottom: 3rem;
}

.mensaje {
  display: flex;
  justify-content: center;
  width: 90%;
  margin: 0 auto;
  border-radius: 0.5rem;
  font-size: 2.5rem;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  background-color: #b0ff8cb9;
}

.swal2-title {
  font-family: "Poppins", sans-serif !important;
}

.swal2-styled.swal2-confirm,
.swal2-styled.swal2-cancel {
  font-family: "Poppins", sans-serif !important;
}

.contenedor__opciones__dashboard {
  width: 100%;
  margin: 2rem auto;
}
@media (min-width: 768px) {
  .contenedor__opciones__dashboard {
    width: 90%;
  }
}
.contenedor__opciones__dashboard .contenedor__opciones {
  display: flex;
  justify-content: space-around;
  font-family: "Poppins", sans-serif;
}
.contenedor__opciones__dashboard .enlaces__opciones {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contenedor__opciones__dashboard .enlaces__opciones a {
  padding: 1rem 1rem;
}
.contenedor__opciones__dashboard .enlaces__opciones a:focus {
  transition: all ease-in 0.5s;
  background-color: #ffffff;
}
.contenedor__opciones__dashboard .enlaces__opciones img {
  height: 28px;
  width: 28px;
}

.mercado .mercado__campo {
  display: flex;
  flex-direction: column;
  margin-bottom: 2.5rem;
}
.mercado .mercado__campo label {
  display: block;
  text-align: center;
  font-weight: bold;
}
.mercado .mercado__campo input[type=text],
.mercado .mercado__campo input[type=number] {
  padding: 0.5rem;
  border: none;
  border-bottom: 1px solid rgba(255, 0, 0, 0.338);
}