.btn-login {
    color: #090909;
    padding: 0.7em 1.7em;
    font-size: 18px;
    font-weight: bold;
    border-radius: 0.5em;
    background: #e8e8e8;
    border: 1px solid #e8e8e8;
    transition: all 0.3s;
    box-shadow: 6px 6px 12px #c5c5c5, -6px -6px 12px #ffffff;

    &:hover {
        border: 1px solid white;
    }

    &:active {
        color: #666;
        box-shadow: inset 4px 4px 12px #c5c5c5, inset -4px -4px 12px #ffffff;
    }
}

.btn-submit {
    color: #090909;
    padding: 0.7em 1.7em;
    font-size: 18px;
    font-weight: bold;
    border-radius: 1em;
    background: #e8e8e8ca;
    border: 1px solid #e8e8e8;
    transition: all 0.3s;

    &:hover {
        border: 1px solid white;
    }

    &:active {
        color: #666;
        box-shadow: inset 4px 4px 12px #c5c5c5, inset -4px -4px 12px #ffffff;
    }
}
